@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap);
.sidebar {
    position: fixed;
    width: 210px;
    background-color: white;
    padding-left: 60px;
    height: 100vh;
}

.sidebar .linie {
    width: calc(100% - 60px);
    height: 1px;
    display: block;
    background-color: #c9c9c9;
}

.sidebarElement,
.sidebar h3 {
    padding: 15px 0;
}


.sidebar h3:nth-child(1) {
    padding-top: 30px;
}

.sidebarElement p {
    position: absolute;
    left: 110px;
}

.sidebarElement {
    display: flex;
    align-items: center;
    cursor: pointer;
}

.sidebarElement:hover .icon{
    color: #256FDF;
}

.sidebarElement:hover p{
    color: #14275A;
}


.sidebar .logo {
    padding-top: 70px;
    padding-bottom: 30px;
    margin-bottom: 0px;
    width: 80%;
}

.sidebarElement p,
.sidebarElement .icon {
    color: #9ba6c4;
}

.sidebarElement .icon {
    color: #9ba6c4;
    font-size: 1.2rem;
}

.sidebar .logoContainer {
    width: calc(100% - 60px);
    display: flex;
    justify-content: center;
}

.sidebar .selected .icon{
    color: #256FDF;
}

.sidebar .selected p{
    color: #14275A;
}

.sidebar .account {
    pointer-events: none;
}

* {
    margin: 0;
    padding: 0;
    font-family: "Montserrat", sans-serif;
}

body {
    background-color: #edf0f6;
}

.userLogo {
    background-color: #9ba6c4;
    height: 1.2rem;
    width: 1.2rem;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.user {
    text-decoration: none;
}

.userLogo p {
    position: static;
    color: white;
    font-size: 0.9rem;
}

p, button {
    font-weight: 600;
    font-size: 1.1rem;
    color: #14275a;
}

h1 {
    font-weight: 600;
    color: #14275a;
    font-size: 4.5rem;
}

h2 {
    font-size: 2.5rem;
    font-weight: 600;
    color: #14275a;
}

h3 {
    font-size: 1.6rem;
    font-weight: 600;
    color: #14275a;
}

h4 {
    font-size: 1.3rem;
    font-weight: 600;
    color: #14275a;
}

.linie {
    height: 1px;
    display: block;
    background-color: #c9c9c9;
}

.app {
    display: flex;
}

.main {
    margin-left: 270px;
    flex-grow: 1;
    display: flex;
    justify-content: center;
    padding: 50px 0;
}

.main .content {
    width: 700px;
    border-right: 1px #c9c9c9 solid;
    border-left: 1px #c9c9c9 solid;

    padding: 25px 50px;
}

.post {
    display: flex;
    background-color: transparent;
    border-radius: 20px;
    border: white 6px solid;
}

.post .mark {
    height: 160px;
    width: 160px;
    background-color: #b2edff;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    z-index: -1;
}

.post .informations {
    overflow: hidden;
    background-color: white;
    flex-grow: 1;
    margin-left: -20px;
    z-index: -2;
    padding: 10px;
    padding-left: 40px;
    position: relative;
}

.post p,
.post h4 {
    color: #9ba6c4;
}

.post h4 {
    margin-top: 5px;
    width: 490px;
    overflow: hidden;
}

.post .date {
    position: absolute;
    bottom: 10px;
    left: 40px;
}

.head {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    border-bottom: 1px #c9c9c9 solid;
    padding-bottom: 15px;
}

input, textarea {
    font-weight: 600;
    font-size: 1.1rem;
    color: #14275a;
    border: solid #9BA6C4 1px;
    border-radius: 5px;
    padding: 7px;
    margin: 1px;
    height: 20px;
}

input::-webkit-input-placeholder {
    color: #9ba6c4;
}

input:-ms-input-placeholder {
    color: #9ba6c4;
}

input::placeholder {
    color: #9ba6c4;
}

input:focus, textarea:focus{
    border: none;
    outline: none;
    border: solid #256fdf 2px;
    margin: 0;
}

.middle {
    text-align: center;
}

button {
    background-color: #256fdf;
    color: white;
    outline: none;
    border: none;
    padding: 7px;
    border-radius: 20px;
    width: 150px;
    cursor: pointer;
}

.home .filter {
    display: flex;
    grid-gap: 10px;
    gap: 10px;
    padding-bottom: 5px;
    cursor: pointer;
}

.home .filter p:hover {
    color: #256fdf;
}

.home .filter .selected {
    color: #256fdf;
}

.posts {
    display: flex;
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
}

.posts > .postComplete {
    margin: 7.5px 0;
}

.posts > .postComplete:first-child {
    margin-top: 25px;
}

.postComplete .userLogo {
    height: 1.4rem;
    width: 1.4rem;
}

.postComplete {
    cursor: pointer;
}

.postComplete .icon {
    font-size: 1.4rem;
}

.postComplete .postInformations {
    display: flex;
    justify-content: space-between;
}

.postComplete .postInformations .user,
.postComplete .postInformations .like {
    display: flex;
    grid-gap: 10px;
    gap: 10px;
    align-items: center;
}

.postComplete .postInformations p:nth-child(2) {
    color: #9ba6c4;
}

.postComplete .postInformations {
    margin: 10px 5px;
    margin-bottom: 0;
}

.content .create .section {
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
    grid-gap: 30px;
    gap: 30px;
}

.content .create .section p {
    margin-bottom: 15px;
}

.content .create .section .description {
    height: 100px;
    width: calc(100% - 16px);
}

textarea {
    resize: none;
}

.content .create .section .divS2 {
    flex-grow: 1;
    width: 100%;
    position: relative;
}

.content .create .section .colors {
    display: flex;
    justify-content: space-between;
    cursor: pointer;
}

.content .create .section .colors .color {
    width: 36px;
    height: 36px;
    border-radius: 50%;
}

.content .create .section .colors .selected {
    border: #707070 3px solid;
    width: 30px;
    height: 30px;
}

.content .create .section .divS2 button{
    position: absolute;
    right: 0;
    bottom: 4px;
}

.content .preview {
    margin-top: 30px;
    border-top: 1px #c9c9c9 solid;
}

.content .preview #title {
    margin: 30px 0;
}




.main .content .profile .userLogo {
    width: 160px;
    height: 160px;
}

.main .content .profile .userLogo p {
    font-size: 6rem;
}

.main .content .profile {
    display: flex;
    grid-gap: 50px;
    gap: 50px;
    align-items: center;
    margin: 25px 0;
}

.main .content .profile .informations {
    flex-grow: 1;
    }

.main .content .profile .informations .title {

   display: flex; 
   justify-content: space-between;
   align-items: center;
   margin-bottom: 20px;
}
.main .unfollow {
    color: #256fdf;
    background-color: white;
    border: 2px solid #256fdf;
}


.main .content .profile .data {
    display: flex;
    justify-content: space-between;
}

.main .content .profile .data .dataElement {
    display: flex;
    align-items: baseline;
    grid-gap: 10px;
    gap: 10px;
}

.main .content .profile .data .dataElement p {
    color: #9ba6c4;
}

.main .content .posts {
    border-top: 1px #c9c9c9 solid;
    padding-top: 25px;
}

.profile input {
    background: url(/NotenFlex_Client/static/media/search.b94db18f.svg) no-repeat scroll 4px 4px;
    background-size: 8%;
    background-position-y: center;
    background-position-x: 7px;
    padding-left: 30px;
}

.main .content .search {
    margin-top: 25px;
}

.main .content .mainInfos {
    display: flex;
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
}


.fullLogin {
    width: 100%;
    z-index: 2;
    display: flex;
}

.login {
    z-index: 1;
    background-color: white;
    height: 100vh;
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 30px;
    gap: 30px;
}

.login h1 {
    color: #256fdf;
}

.login .linie {
    width: 300px;
}

.login button {
    width: 300px;
}

.login .input {
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
}

.login .button p {
    text-align: center;
}

.login .grey button {
    background-color: #9ba6c4;
}

.presentation {
    width: 50%;
    background-color: #edf0f6;
    position: relative;
    overflow: hidden;
    height: 100vh;
}

.presentation .circle {
    width: 500px;
    height: 500px;
    background-color: #256fdf;
    opacity: 10%;
    border-radius: 50%;
    position: absolute;
    right: -200px;
    bottom: -250px;
}

.presentation .square {
    width: 120px;
    height: 120px;
    background-color: #256fdf;
    opacity: 10%;
    position: absolute;
    transform: rotate(-106deg);
    right: 100px;
    top: 200px;
}

.presentation .triangle {
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 75px 130px 75px;
    border-color: transparent transparent rgba(37, 111, 223, 0.1)transparent;
    position: absolute;
    transform: rotate(27deg);
    bottom: 200px;
    left: 100px;
}

.presentation img {
    width: 250px;
    margin-bottom: 90px;
}


.presentation .infos {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 90px;
}

.presentation h3 {
    text-align: left;
    color: #256fdf;
    margin-left: 50px;
}

.description {
    margin-right: 200px;
    display: flex;
    flex-direction: column;
    grid-gap: 30px;
    gap: 30px;
}

.presentation .linie {
    width: 100%;
}

@media only screen and (max-width: 1300px) {

    .login {
        width: 100%;
    }

    .fullLogin {
        flex-direction: column;
    }

    .presentation {
        width: 100%;
    }
}

.log {
    width: 100%;
}

